.about_container {
        grid-template-columns: 3fr 9fr;
        column-gap: 1.875rem;
}

.about_data {
    padding: 1.875rem;
    background-color: var(--container-color);
    box-shadow: var(--shadow);
    border-radius: var(--border-radius);
    grid-template-columns: repeat(2,1fr);
    column-gap:1.875rem;
    align-items: flex-start;
    position:relative;
}   

.about_data::before{
        content:'';
        width: 0;
        height:0;
}